import { Component, OnInit, OnDestroy } from '@angular/core';
import { Serie } from 'src/app/services/interfaces/serie';
import { DataSerieService } from 'src/app/services/data-serie.service';
import { NavController } from '@ionic/angular';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-series',
	templateUrl: './series.page.html',
	styleUrls: [ './series.page.scss' ]
})
export class SeriesPage implements OnInit, OnDestroy {
	public mesSeries: Serie[];
	public searchInput: string = null;
	private serieSub: Subscription;

	constructor(private navCtrl: NavController, private serieService: DataSerieService) {
		this.mesSeries = [ ...serieService.mySerie ];
	}

	isFinished(s: Serie) {
		return !(s.dateF === undefined);
	}

	ngOnInit() {
		this.serieSub = this.serieService.mySerieChanged.subscribe((serie) => {
			this.mesSeries = serie;
		});
	}

	ngOnDestroy() {
		this.serieSub.unsubscribe();
	}

	goToAjouter() {
		this.navCtrl.navigateForward([ '/selectionner-serie' ]);
	}

	showInfo(id: number) {
		this.navCtrl.navigateForward([ '/info-serie/' + id ]);
	}
}
