import { Component, OnInit } from '@angular/core';
import { Serie } from 'src/app/services/interfaces/serie';
import { Router } from '@angular/router';
import { DataSerieService } from 'src/app/services/data-serie.service';
import { AlertController } from '@ionic/angular';

@Component({
	selector: 'app-series',
	templateUrl: './series.page.html',
	styleUrls: [ './series.page.scss' ]
})
export class SeriesPage implements OnInit {
	public searchInput: string;
	public seriesRec: Serie[];

	constructor(private router: Router, private serieService: DataSerieService, private alertCtrl: AlertController) {
		this.seriesRec = [ ...serieService.recSerie ];
	}

	ngOnInit() {
		this.seriesRec = this.shuffle(this.seriesRec);
	}

	shuffle(array) {
		var currentIndex = array.length,
			temporaryValue,
			randomIndex;
		while (0 !== currentIndex) {
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex -= 1;
			temporaryValue = array[currentIndex];
			array[currentIndex] = array[randomIndex];
			array[randomIndex] = temporaryValue;
		}
		return array;
	}

	isFinished(s: Serie) {
		return !(s.dateF === undefined);
	}

	async supprimer(s: Serie) {
		const alert = await this.alertCtrl.create({
			header: 'Suprimer une SÃ©rie',
			message: 'Voulez vous vraiment supprimer cette sÃ©rie de vos recommandations ?',
			buttons: [
				'Annuler',
				{
					text: 'Valider',
					role: 'valider',
					handler: () => {
						var i: number = this.seriesRec.indexOf(s);
						this.seriesRec.splice(i, 1);
						this.serieService.delInRecSerie(s);
					}
				}
			]
		});

		await alert.present();
	}
}
